	module.exports = {"data":{"post":{"html":"<p>Twins pitcher and Minnesota native Pat Neshek has been keeping a <a href=\"http://patneshek.com/\">weblog</a> since he was in the minors. It's now spring training and Pat will be starting his second full year in the major leagues. The Twins now figure that Pat is well known enough to star in his own <a href=\"http://www.youtube.com/watch?v=Fv6pYVNxJQI\">TV ad</a> for the team. Well, maybe he's not as well known as they thought...</p>\n<p><object width=\"425\" height=\"355\"><param name=\"movie\" value=\"http://www.youtube.com/v/Fv6pYVNxJQI&hl=en\" /><param name=\"wmode\" value=\"transparent\" /><embed src=\"http://www.youtube.com/v/Fv6pYVNxJQI&hl=en\" type=\"application/x-shockwave-flash\" wmode=\"transparent\" width=\"425\" height=\"355\"></embed></object></p>\n<p>Pat himself <a href=\"http://eteamz.active.com/PatNeshek/#news4228830\">blogged about the spot</a> (but click fast, his hoster hasn't implemented permalinks), and if you're not from Minnesota you might need to <a href=\"http://www.google.com/search?q=joe+mauer+sideburns\">Google</a> to get the bit about the sideburns. All of the Twins TV ads are pretty good... the best one was last year with the <a href=\"http://www.youtube.com/watch?v=_7G3OtU6OkQ\">Johan Santana/Joe Nathan carpool</a>.</p>","fields":{"title":"Twins Ad with Pat Neshek","date":"18 Mar 2008","url":"https://papascott.de/2008-03-18-twins-ad-with-pat-neshek/"}}},"pathContext":{"slug":"/2008-03-18-twins-ad-with-pat-neshek/","prev":{"fields":{"title":"Stroke of Insight","date":"15 Mar 2008","slug":"/2008-03-15-stroke-of-insight/"}},"next":{"fields":{"title":"Ship Ahoy","date":"24 Mar 2008","slug":"/2008-03-24-ship-ahoy/"}}}}

